

<template>
    <div>
        <common-header
            tab="user"
        />
        <center-form 
            type="login"
        />
    </div>

</template>

<script>
import Vue from "Vue"
import Header from "../components/Common-header.vue"
import Form from "../components/Center-form.vue"
import axios from "axios"
import { Message } from 'element-ui'

axios.interceptors.response.use( res => {return res.data})

const loginUrl = "ajaxlogin";

    export default {
        data () {
            return {
                Bus: new Vue()
            }
        },
        components: {
            "common-header": Header,
            "center-form" : Form
        },

        computed: {
            
        },
        methods: {
            
        },
        created () {
            document.title = "登录"
            this.Bus.$on("submit", function(params){
               axios.get(loginUrl,{params: params})
                    .then(function(res){
                        if(res.status == 1){
                            Message.success("登录成功！")
                            window.location.href = res.data.go
                        }else{
                            Message.error(res.info)
                        }
                    })
            })
        }
    }
</script>

<style>
    @import "../../static/css/login.css"
</style>


